import { mapState } from "vuex";
export default {
  name: "now",
  computed: {
    ...mapState({
      settings: state => {
        return state.settings;
      }
    })
  },
  methods: {
    saveSettings() {
      const settings = {
        token: settings.token,
        apiUrl: settings.apiUrl,
        user: settings.user,
        repo: settings.repo
      };
      this.$q.localStorage.set("lee-todo-settings", settings);
      this.$store.commit("initSettings", { settings: settings });
      this.$router.push({
        path: "/now"
      });
    }
  }
};
